/* Border-box-sizing */
@import url(fonts.css);
.block-group, .block, .block-group:after, .block:after, .block-group:before, .block:before {
  box-sizing: border-box; }

/* Clearfix */
.block-group {
  *zoom: 1; }

.block-group:before, .block-group:after {
  display: table;
  content: "";
  line-height: 0; }

.block-group:after {
  clear: both; }

.block-group {
  /* ul/li compatibility */
  list-style-type: none;
  padding: 0;
  margin: 0; }

/* Nested grid */
.block-group > .block-group {
  clear: none;
  float: left;
  margin: 0 !important; }

/* Default block */
.block {
  float: left;
  width: 100%; }

/* MailChimp Form Embed Code - Slim - 12/15/2015 v10.7 */
#mc_embed_signup form {
  display: block;
  position: relative;
  text-align: left;
  padding: 10px 0 10px 3%; }

#mc_embed_signup h2 {
  font-weight: bold;
  padding: 0;
  margin: 15px 0;
  font-size: 1.4em; }

#mc_embed_signup input {
  border: 1px solid #999;
  -webkit-appearance: none; }

#mc_embed_signup input[type=checkbox] {
  -webkit-appearance: checkbox; }

#mc_embed_signup input[type=radio] {
  -webkit-appearance: radio; }

#mc_embed_signup input:focus {
  border-color: #333; }

#mc_embed_signup .button {
  clear: both;
  background-color: #aaa;
  border: 0 none;
  border-radius: 4px;
  letter-spacing: .03em;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-size: 15px;
  height: 32px;
  line-height: 32px;
  margin: 0 5px 10px 0;
  padding: 0;
  text-align: center;
  text-decoration: none;
  vertical-align: top;
  white-space: nowrap;
  width: auto;
  transition: all 0.23s ease-in-out 0s; }

#mc_embed_signup .button:hover {
  background-color: #777; }

#mc_embed_signup .small-meta {
  font-size: 11px; }

#mc_embed_signup .nowrap {
  white-space: nowrap; }

#mc_embed_signup .clear {
  clear: none;
  display: inline; }

#mc_embed_signup label {
  display: block;
  font-size: 16px;
  padding-bottom: 10px;
  font-weight: bold; }

#mc_embed_signup input.email {
  font-family: "Open Sans","Helvetica Neue",Arial,Helvetica,Verdana,sans-serif;
  font-size: 15px;
  display: block;
  padding: 0 0.4em;
  margin: 0 4% 10px 0;
  min-height: 32px;
  width: 58%;
  min-width: 130px;
  border-radius: 3px; }

#mc_embed_signup input.button {
  display: block;
  width: 35%;
  margin: 0 0 10px 0;
  min-width: 90px; }

#mc_embed_signup div#mce-responses {
  float: left;
  top: -1.4em;
  padding: 0em .5em 0em .5em;
  overflow: hidden;
  width: 90%;
  margin: 0 5%;
  clear: both; }

#mc_embed_signup div.response {
  margin: 1em 0;
  padding: 1em .5em .5em 0;
  font-weight: bold;
  float: left;
  top: -1.5em;
  z-index: 1;
  width: 80%; }

#mc_embed_signup #mce-error-response {
  display: none; }

#mc_embed_signup #mce-success-response {
  color: #529214;
  display: none; }

#mc_embed_signup label.error {
  display: block;
  float: none;
  width: auto;
  margin-left: 1.05em;
  text-align: left;
  padding: .5em 0; }

.container {
  width: 85vw;
  margin: 0 auto; }

body {
  margin: 0;
  font-size: 100%;
  font-family: 'Karla', sans-serif;
  color: #e0e0e0;
  background-color: #101010;
  line-height: 1.6;
  box-sizing: border-box; }

h1 {
  font-family: 'Pacifico', cursive;
  color: #ffc600; }

h2 {
  font-family: 'BebasNeue-Regular', sans-serif;
  letter-spacing: 0.05em;
  color: #e0e0e0;
  font-size: 2.5em;
  margin-bottom: 0; }

h4, .question {
  font-weight: 600;
  color: #ff2520;
  font-style: italic; }

.eventinfo {
  text-align: center; }

a {
  color: #ffc600;
  text-decoration: none; }
  a:hover, a:focus {
    color: #ffcc1a;
    text-decoration: underline; }

header {
  text-align: center;
  margin: 3em 0; }
  header img {
    max-width: 100%;
    margin: 0 auto;
    display: block; }

.intro {
  font-weight: 500;
  font-size: 1.5em;
  margin-bottom: 3em; }

#mc_embed_signup {
  padding: 0 0 2em 0;
  font-family: inherit !important; }
  #mc_embed_signup form {
    padding: 0; }
  #mc_embed_signup label {
    color: #ff2520; }
  #mc_embed_signup input.email {
    font-family: inherit; }
  #mc_embed_signup input.button {
    font-family: inherit;
    background-color: #ffc600;
    color: #664f00; }
    #mc_embed_signup input.button:hover, #mc_embed_signup input.button:focus {
      background-color: #ffcc1a; }

footer {
  margin-top: 2em;
  padding: 1em 0;
  text-align: center;
  color: #a0a0a0;
  background-color: #1a1a1a; }

/* Larger tables and laptops/desktop. Limit width for typography purposes */
@media (min-width: 780px) {
  .container {
    max-width: 768px;
    margin: 0 auto; }
  header {
    margin: 4em 0; }
    header img {
      max-width: 80%; }
  .block {
    width: 75%; }
  .headline {
    width: 25%; } }
